function fitValidator(fit) {
  return ['fill', 'contain', 'cover', 'none', 'scale-down'].includes(fit);
}

export var props = {
  src: {
    type: String
  },
  fit: {
    type: String,
    validator: fitValidator,
    default: 'cover'
  },

  /**
   * @deprecated use imageHeight instead
   */
  height: {
    type: [String, Number]
  },
  imageHeight: {
    type: [String, Number]
  },
  imageWidth: {
    type: [String, Number]
  },
  layout: {
    type: String,
    default: 'column'
  },
  floating: {
    type: Boolean,
    default: false
  },
  'onUpdate:floating': {
    type: Function
  },
  floatingDuration: {
    type: Number,
    default: 250
  },
  alt: {
    type: String
  },
  title: {
    type: String
  },
  subtitle: {
    type: String
  },
  description: {
    type: String
  },
  elevation: {
    type: [Number, String]
  },
  ripple: {
    type: Boolean,
    default: false
  },
  onClick: {
    type: Function
  }
};