function positionValidator(position) {
  return ['top', 'bottom', 'right', 'left', 'center'].includes(position);
}

export var props = {
  show: {
    type: Boolean,
    default: false
  },
  position: {
    type: String,
    default: 'center',
    validator: positionValidator
  },
  transition: {
    type: String
  },
  overlay: {
    type: Boolean,
    default: true
  },
  overlayClass: {
    type: String
  },
  overlayStyle: {
    type: Object
  },
  lockScroll: {
    type: Boolean,
    default: true
  },
  closeOnClickOverlay: {
    type: Boolean,
    default: true
  },
  teleport: {
    type: String
  },
  onOpen: {
    type: Function
  },
  onOpened: {
    type: Function
  },
  onClose: {
    type: Function
  },
  onClosed: {
    type: Function
  },
  onClickOverlay: {
    type: Function
  },
  // internal for Dialog
  onRouteChange: {
    type: Function
  },
  'onUpdate:show': {
    type: Function
  }
};